import React from 'react';
import ReactDOM from 'react-dom'
import "./log.scss"
import {Link,Router,Route,IndexRoute} from 'react-router';

class Log extends React.Component{
    render(){
        return(
                <section className="log">
                    <h3>饿了么</h3>
                    <ul>
                        <li><Link to="/PhoneLog" activeStyle={{color:"#2395FF",borderBottom:"1px solid #2395FF"}}>注册</Link></li>
                        <li><Link to="/PasswordLog" activeStyle={{color:"#2395FF",borderBottom:"1px solid #2395FF"}}>登录</Link></li>
                    </ul>
                    {this.props.children}
                    </section>
        )
    }
}
export default Log





