import React from 'react'
import './ShoppingCar.scss'
class ShoppingCar extends React.Component{
    constructor(props) {
        super(props)
        this.handleClick = this.handleClick.bind(this)
        this.state = {
            id: '',
            name: '',
            menu: [{name: '凉皮', number: '2', price: '15'}, {name: '凉皮', number: '2', price: '15'}, {
                name: '凉皮',
                number: '2',
                price: '15'
            }],
            order: [],
            info: [],
            style: {right: "-16em"}
        }
    }
    //处理单击
    handleClick(e){
        var tag=e.target.id;
        var text=document.getElementById(e.target.id).innerText;
        var styleChange=(this.state.style.right =="16em")?"0":"16em"
        this.setState({id:tag,name:text,style:styleChange})
    }
    //创建XHR
    createXHR(){
        if(typeof XMLHttpRequest != 'undefined'){
            return new XMLHttpRequest();
        }
        else if(typeof ActiveXObject != 'undefined'){
            if(typeof arguments.callee.activeXString != 'string'){
                var versions=['MSXML2.XMLHttp.6.0','MSXML2.XMLHttp.3.0','MSXML.XMLHttp'],i,len;
                for(i=0,len=versions.length;i<len;i++){
                    try{
                        new ActiveXObject(versions[i]);
                        arguments.callee.activeXString =versions[i];
                        break;
                    }catch (e){
                    }
                }

            }
            return new ActiveXObject(arguments.callee.activeXString)
        }else{
            throw new Error("No XHR object available.")
        }
    }
    //发起数据请求
    ajax(){
        var xhr=this.createXHR();
        xhr.onreadystatechange=function(){
            if(xhr.readyState == 4){
                if((xhr.status >= 200 && xhr.status <300) || xhr.status == 304) {
                    return xhr.responseText
                }else{
                    alert("Request was fail"+xhr.status)
                }
            }
        }
        xhr.open('get','example.txt',true);
        xhr.send(null);
    }

    render(){
        let arr=[];
        let menuList='';
        switch (this.state.id){
            case 'order': return menuList=this.state.order;
                break;
            case 'shopping_car':return menuList=this.state.menu;
                break;
            case 'info':return menuList=this.state.info;
                break;
            default:break;
        }
        if(menuList!==''){
            for(var i=0,len=menuList.length;i<len;i++){
                arr.push(<tr><td>{menuList[i].name}</td><td>{menuList[i].number}</td><td>{menuList[i].price * menuList[i].number}</td></tr>)
            }
        }
        return (
            <article className='shopping_Car' style={this.state.style}>
             <section className='navBar'>
                <ul>
                    <li id='order' onClick={this.handleClick}> 我的订单</li>
                    <li id='shopping_car' onClick={this.handleClick}>购物车</li>
                    <li id='info' onClick={this.handleClick}>我的信息</li>
                    <li>App下载</li>
                </ul>
            </section>
                <section className='content'>
                <h6>{this.state.name}</h6>
                <table>{arr}</table>
            </section>
        </article>)
    }
}
export default ShoppingCar


